* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    cursor: pointer;
    overflow: hidden;
}

.wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box:nth-child(1) {
    filter: blur(5px);
}

.box:nth-child(2) {
    clip-path: circle(100px at var(--x) var(--y));
}

.circle {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 2px solid #fff;
    border-radius: 50%;
    box-shadow: 0 5px 23px rgba(0, 0, 0, 0.5);
    top: -100px;
    left: -100px;
    transform: translate(var(--x), var(--y));
}

.notOpen {
    padding: 2.5rem;
}

.desc {
    padding: 1rem;
}

.author {
    padding: 1rem;
}